import React, { useEffect, useState } from 'react';
import './style.less';
// 
const ToChineseNum = () => {
  const [number, setNumber] = useState();
  const handleChange = (e) => {
    const { value } = e?.target;
    if (/^\d*$/.test(value)) setNumber(value);
  };

  const handleZero = (str) => {
    return str.replace(/+/g, '').replace(/$/, '');
  };

  const [CNCount, setCNCount] = useState('');
  const handleBlur = () => {
    const parts = number
      .toString()
      .replace(/(?=(\d{4})+$)/g, ',')
      .split(',')
      .filter(Boolean);
    const map = ['', '', '', '', '', '', '', '', '', ''];
    const units = ['', '', '', ''];
    function _transform(n) {
      let result = '';
      for (const i in n) {
        let unit = units[n.length - i - 1];
        let char = map[n[i]];
        // 
        if (char === '') unit = '';
        result += char + unit;
      }
      result = handleZero(result);
      return result;
    }

    const bigUnits = ['', '', ''];
    let result = '';
    for (const i in parts) {
      const char = _transform(parts[i]);
      if (char === '') {
        result += '';
        continue;
      }
      let unit = bigUnits[parts.length - i - 1];
      result += char + unit;
    }
    result = handleZero(result);
    setCNCount(result);
  };

  return (
    <div className="toChineseNum">
      
      <input
        value={number}
        onChange={handleChange}
        onBlur={handleBlur}
        placeholder=""
      />
      {CNCount && <div className="amount">{CNCount}</div>}
    </div>
  );
};

export default ToChineseNum;
