import {
  AmbientLight,
  Clock,
  Color,
  DirectionalLight,
  HemisphereLight,
  PerspectiveCamera,
  Scene,
  Spherical,
  Vector3,
  WebGLRenderer,
} from 'three';
import { OrbitControls } from 'three/addons/controls/OrbitControls.js';

const createSceneObject = function ({ radius, width, height }) {
  const scene = new Scene();
  scene.background = new Color('#010826');

  /**
   * 
   */
  const ambientLight = new HemisphereLight(0xffffff, 0xffffff, 1);
  // const ambientLight = new AmbientLight(0xffffff, 10);
  ambientLight.castShadow = true;
  /**
   * 
   */
  const directionalLight = new DirectionalLight(0xffffff, 0.5);
  directionalLight.castShadow = true;

  /**
   * 
   */
  const camera = new PerspectiveCamera(45, width / height, 1, 10000);
  // radius 
  // phi 
  // theta 
  const startCameraSpherical = new Spherical();
  startCameraSpherical.radius = 2.5 * radius;
  startCameraSpherical.phi = Math.PI / 3;
  startCameraSpherical.theta = (-8 / 9) * Math.PI;
  // 
  const cameraPosition = new Vector3();
  cameraPosition.setFromSpherical(startCameraSpherical);
  // camera.position.set(cameraPosition.x, cameraPosition.y, cameraPosition.z);
  camera.position.set(0, 30, 400);
  camera.zoom = 1;

  /**
   * 
   */
  const renderer = new WebGLRenderer({
    alpha: true,
    antialias: true,
  });
  renderer.setSize(width, height);
  // 
  renderer.setClearColor(0x050d2a, 0);
  // 
  renderer.shadowMap.enabled = true;
  renderer.setPixelRatio(window.devicePixelRatio);

  /**
   *   
   */
  const clock = new Clock();

  /**
   * 
   */
  const control = new OrbitControls(camera, renderer.domElement);
  control.enabled = true;
  // zoom 
  control.minDistance = 150;
  control.maxDistance = 550;
  // 
  control.autoRotate = true;
  control.autoRotateSpeed = 0.6;
  // 
  control.enableZoom = true;
  control.zoomSpeed = 1.0;

  // 
  control.enablePan = false;
  // 
  control.panSpeed = 1.0;

  // 
  control.enableRotate = true;
  control.rotateSpeed = 0.3;
  // 
  control.target = new Vector3(0, 0, 0);

  return {
    scene,
    camera,
    renderer,
    control,
    ambientLight,
    directionalLight,
    clock,
    canvas: renderer.domElement,
  };
};

export { createSceneObject };
