import {
  BufferGeometry,
  ExtrudeGeometry,
  Line,
  Mesh,
  MeshBasicMaterial,
  MeshPhongMaterial,
  Object3D,
  Shape,
  Vector3,
  Group,
} from 'three';
import ChinaJson from '@/static/json/china.json';
import * as d3 from 'd3';

// 
const rayList = new Group();

const addHexagon = (elem) => {
  if (elem?.level !== 'province') return;
  const { centroid } = elem;
  if (centroid === undefined) return;
  const [x, y] = projection(centroid);
  // console.log(x);
  // console.log(y);
  // console.log('----');
};

/***
 * 
 */
const CNMap = new Object3D();

// ()
const projection = d3
  .geoMercator()
  .center([116.23128, 40.22077])
  .translate([10, -18]);
ChinaJson?.features.forEach((elem) => {
  const province = new Object3D();
  province.name = elem.properties.name;
  province.properties = elem.properties;
  // 
  addHexagon(elem.properties);
  elem?.geometry.coordinates.forEach((multiPolygon) => {
    // 
    multiPolygon.forEach((polygon) => {
      const shape = new Shape();

      const points = [];
      for (let i = 0; i < polygon.length; i++) {
        const [x, y] = projection(polygon[i]); // 
        if (i === 0) shape.moveTo(x, -y);
        shape.lineTo(x, -y);
        points.push(new Vector3(x, -y, 0)); //;
      }
      // 
      const lineMaterial = new MeshBasicMaterial({
        color: '#05f6e7',
      });
      const lineGeometry = new BufferGeometry();
      //  BufferGeometry  attribute
      lineGeometry.setFromPoints(points);
      const line = new Line(lineGeometry, lineMaterial);
      line.name = elem.properties.name;

      // 
      province.add(line);
      // 
      const extrGeometry = new ExtrudeGeometry(shape, {
        depth: 1,
        bevelEnabled: false,
      });
      const material = new MeshPhongMaterial({
        color: '#0b96a5',
        transparent: true,
        opacity: 0.8,
      });
      const provinceMesh = new Mesh(extrGeometry, [material, material]);
      //  
      province.add(provinceMesh);
    });
  });
  CNMap.add(province);
});

export { CNMap, rayList };
