import { fileUpload, fileMerge } from '@/api/Interface/home/upload';
import Promise from '@/components/Promise';

/**
 * 
 * @param {*} fileInfo 
 */

const concurrencyUpload = async function (fileInfo) {
  let pool = [],
    max = 3;
  const { filename, fileChunks } = fileInfo;

  // 
  await fileChunks.forEach(async (file) => {
    let form = new FormData();
    form.append('filename', filename);
    form.append('hash', file.hash);
    form.append('chunk', file.chunk);
    const task = fileUpload(form);
    pool.push(task);
    // 
    task.then(() => {
      // Promise
      const index = pool.findIndex(task);
      pool.splice(index, 1);
    });
    // 
    if (pool.length === max) await Promise.race(pool);
  });
  await fileMerge({ filename });
};

export default concurrencyUpload;
