import {
  CatmullRomCurve3,
  DoubleSide,
  Group,
  Mesh,
  MeshBasicMaterial,
  PlaneGeometry,
  Spherical,
  TubeGeometry,
  Vector3,
} from 'three';

/**
 * ()
 * @param {*} R 
 * @param {*} longitude 
 * @param {*} latitude 
 */
export const lon2xyz = (R, longitude, latitude) => {
  let lon = (longitude * Math.PI) / 180; // 
  const lat = (latitude * Math.PI) / 180; // 
  lon = -lon; // jsz-9090

  // 
  const x = R * Math.cos(lat) * Math.cos(lon);
  const y = R * Math.sin(lat);
  const z = R * Math.cos(lat) * Math.sin(lon);

  // 
  return new Vector3(x, y, z);
};

/**
 * ()
 * @param {*} R 
 * @param {*} longlat 
 * @returns
 */
export const createPosition = (R, longlat) => {
  const spherical = new Spherical();
  spherical.radius = R;
  const lng = longlat[0];
  const lat = longlat[1];
  const theta = (lng + 90) * (Math.PI / 180);
  const phi = (90 - lat) * (Math.PI / 180);
  spherical.phi = phi;
  spherical.theta = theta;
  const position = new Vector3();
  position.setFromSpherical(spherical);
  return position;
};

export const getSplinePoints = (radius, positions) => {
  const [start, end] = positions;
  const points = [];
  points[0] = new Vector3(start.x, start.y, start.z);
  points[8] = new Vector3(end.x, end.y, end.z);
  const getPoint = (point1, point2) => {
    const tempMiddle = new Vector3(
      0.5 * (point1.x + point2.x),
      0.5 * (point1.y + point2.y),
      0.5 * (point1.z + point2.z),
    );
    const tempMiddleLength = Math.sqrt(
      tempMiddle.x ** 2 + tempMiddle.y ** 2 + tempMiddle.z ** 2,
    );
    const rate = (radius + 1) / tempMiddleLength;
    const middle = new Vector3(
      rate * tempMiddle.x,
      rate * tempMiddle.y,
      rate * tempMiddle.z,
    );
    return middle;
  };
  points[4] = getPoint(points[0], points[8]);
  points[2] = getPoint(points[0], points[4]);
  points[1] = getPoint(points[0], points[2]);
  points[3] = getPoint(points[2], points[4]);
  points[6] = getPoint(points[4], points[8]);
  points[5] = getPoint(points[4], points[6]);
  points[7] = getPoint(points[6], points[8]);

  const origin = points[0];
  const target = points[8];
  const middlePoints = points.slice(1, 8);
  const temp1 = getPoint(origin, middlePoints[0]);
  const temp2 = getPoint(middlePoints[6], target);

  return [origin, temp1, ...middlePoints, temp2, target];
};

// 
export const createPointMesh = (options) => {
  const geometry = new PlaneGeometry(1, 1); //XOY
  const mesh = new Mesh(geometry, options.material);
  // 
  const coord = lon2xyz(options.radius * 1.001, options.lon, options.lat);
  const size = options.radius * 0.05; // Mesh
  mesh.scale.set(size, size, size); // mesh

  // mesh
  mesh.position.set(coord.x, coord.y, coord.z);
  // 
  const coordVec3 = new Vector3(coord.x, coord.y, coord.z).normalize();
  const meshNormal = new Vector3(0, 0, 1);
  mesh.quaternion.setFromUnitVectors(meshNormal, coordVec3);
  return mesh;
};

// 
export const createLightPillar = (options) => {
  const height = options.radius * 0.3;
  const geometry = new PlaneGeometry(options.radius * 0.05, height);
  geometry.rotateX(Math.PI / 2);
  geometry.translate(0, 0, height / 2);
  const material = new MeshBasicMaterial({
    map: options.textures.light_column,
    color:
      options.index == 0
        ? options.punctuation.lightColumn.startColor
        : options.punctuation.lightColumn.endColor,
    transparent: true,
    side: DoubleSide,
    depthWrite: false, //
  });

  const mesh = new Mesh(geometry, material);
  const group = new Group();
  // 
  group.add(mesh, mesh.clone().rotateZ(Math.PI / 2)); //xmeshz
  // 
  const SphereCoord = lon2xyz(options.radius, options.lon, options.lat); //SphereCoord
  group.position.set(SphereCoord.x, SphereCoord.y, SphereCoord.z); //mesh
  // 
  const coordVec3 = new Vector3(
    SphereCoord.x,
    SphereCoord.y,
    SphereCoord.z,
  ).normalize();
  const meshNormal = new Vector3(0, 0, 1);
  group.quaternion.setFromUnitVectors(meshNormal, coordVec3);
  return group;
};

// 
export const createWaveMesh = (options) => {
  const geometry = new PlaneGeometry(1, 1); //XOY
  const texture = options.textures.aperture;
  const material = new MeshBasicMaterial({
    color: 0xe99f68,
    map: texture,
    transparent: true, //png
    opacity: 1.0,
    depthWrite: false, //
  });
  const mesh = new Mesh(geometry, material);

  // 
  const coord = lon2xyz(options.radius * 1.001, options.lon, options.lat);
  const size = options.radius * 0.12; //Mesh
  mesh.scale.set(size, size, size); //mesh
  mesh.userData['size'] = size; //mesh
  mesh.userData['scale'] = Math.random() * 1.0; //._smesh  mesh.size1~2
  mesh.position.set(coord.x, coord.y, coord.z);
  // 
  const coordVec3 = new Vector3(coord.x, coord.y, coord.z).normalize();
  const meshNormal = new Vector3(0, 0, 1);
  mesh.quaternion.setFromUnitVectors(meshNormal, coordVec3);
  return mesh;
};

// 
export const getCirclePoints = (option) => {
  const list = [];
  for (
    let j = 0;
    j < 2 * Math.PI - 0.1;
    j += (2 * Math.PI) / (option.number || 100)
  ) {
    list.push([
      parseFloat((Math.cos(j) * (option.radius || 10)).toFixed(2)),
      0,
      parseFloat((Math.sin(j) * (option.radius || 10)).toFixed(2)),
    ]);
  }
  if (option.closed) list.push(list[0]);
  return list;
};

/**
 * 
 */
export const createAnimateLine = (option) => {
  //  
  const l = [];
  option.pointList.forEach((e) => l.push(new Vector3(e[0], e[1], e[2])));
  const curve = new CatmullRomCurve3(l); // 

  // 
  const tubeGeometry = new TubeGeometry(
    curve,
    option.number || 50,
    option.radius || 1,
    option.radialSegments,
  );
  return new Mesh(tubeGeometry, option.material);
};
