import { fileUpload, fileMerge } from '@/api/Interface/home/upload';
import Promise from '@/components/Promise';

/**
 * 
 * @param {*} fileInfo 
 */
const resumeUpload = async function (fileInfo) {
  const pool = [],
    max = 3;
  const { filename, fileChunks } = fileInfo;

  // 
  const doUpload = async function (list) {
    if (!list.length) {
      await fileMerge({ filename });
    }
    let failList = [],
      finish = 0;

    list.forEach(async (file) => {
      const form = new FormData();
      form.append('filename', filename);
      form.append('hash', file.hash);
      form.append('chunk', file.chunk);
      const task = fileUpload(form);
      // 
      pool.push(task);
      // 
      if (pool.length === max) await Promise.race(pool);
      // 
      task
        .then(() => {
          // Promise
          const index = pool.findIndex(task);
          pool.splice(index, 1);
        })
        .catch(() => () => {
          // 
          failList.push(task);
        })
        .finally(() => {
          finish++;
          if (finish === list.length) {
            doUpload(failList);
          }
        });
    });
  };
  await doUpload(fileChunks);
};

export default resumeUpload;
