import { GetUsefulParams } from '@/utils';
import Promise from '@/components/Promise';
import Interceptor from './Interceptor';

//
export const Timer = 10;
export const Prefix = process.env.NODE_ENV === 'production' ? '' : '/api';

class axios {
  constructor() {
    this.interceptor = {
      request: new Interceptor(),
      response: new Interceptor(),
    };
  }
  /**
   * 
   * @param {*} config
   */
  request(config) {
    // ajax
    const PromiseChain = [
      {
        resolved: this.send.bind(this),
        rejected: undefined,
      },
    ];

    // 
    this.interceptor.request.handle.forEach((interceptor) =>
      PromiseChain.unshift(interceptor),
    );

    // 
    this.interceptor.response.handle.forEach((interceptor) =>
      PromiseChain.push(interceptor),
    );

    // 
    let promise = Promise.resolve(config);
    while (PromiseChain.length) {
      const { resolved, rejected } = PromiseChain.shift();
      // request, response
      promise = promise.then(resolved, rejected);
    }
    return promise;
  }

  /**
   * xmlhttprequest
   * request
   * @param {*} config
   * @returns
   */
  send(config) {
    return new Promise((resolve, reject) => {
      let {
        method = 'GET',
        url = '',
        data = null,
        responseType,
        async = true,
        headers = null,
      } = config;

      const XHR = new XMLHttpRequest();
      // 
      if (responseType) XHR.responseType = responseType;

      // 
      const IsForm =
        Object.prototype.toString.call(data) === '[object FormData]';

      // 
      url = Prefix + url;
      // 
      if (data && !IsForm) data = GetUsefulParams({ ...data });
      // Get,,url
      const GetChain = ['GET', 'DELETE', 'OPTIONS'].includes(method);
      if (GetChain && data && !!Object.keys(data).length) {
        url += '?';
        Object.keys(data).forEach((key, index) => {
          if (index === Object.keys(data).length - 1) {
            url += `${key}=${data[key]}`;
          } else {
            url += `${key}=${data[key]}&`;
          }
        });
      }
      // 
      XHR.open(method.toUpperCase(), url, async);

      // 
      if (headers && Object.keys(headers).length) {
        Object.keys(headers).forEach((name) => {
          if (data === null && name.toLocaleLowerCase() === 'content-type') {
            delete headers[name];
          }
          XHR.setRequestHeader(name, headers[name]);
        });
      }
      XHR.timeout = Timer * 1000;
      XHR.setRequestHeader('If-Modified-Since', 0);
      XHR.setRequestHeader('withCredentials', true);
      XHR.setRequestHeader('Accept', 'application/json, text/plain, */*');
      !IsForm &&
        XHR.setRequestHeader('Content-type', 'application/json;charset=UTF-8');
      XHR.setRequestHeader('Accept-Language', localStorage.language || 'CHN');
      // Token
      if (sessionStorage.authToken) {
        XHR.setRequestHeader(
          'Authorization',
          `Bearer ${sessionStorage.authToken}`,
        );
      }

      // 
      // readyState
      // 0: 
      // 1: 
      // 2: 
      // 3: 
      // 4: 
      // status
      // 200: "OK"
      // 404: 
      XHR.onreadystatechange = () => {
        if (XHR.readyState !== 4) return;
        // blob 
        const responseHeaders = XHR.getAllResponseHeaders();
        const token = XHR.getResponseHeader('authorization');
        // const responseData =
        //   responseType !== 'blob' ? JSON.parse(XHR.response) : XHR.response;
        const responseData =
          responseType !== 'blob' ? XHR.response : XHR.response;
        //   Blob 
        if (responseType && responseType === 'blob') {
          const reader = new FileReader();
          reader.readAsDataURL(XHR.response);
          reader.onload = function (e) {
            let filename;
            if (data?.fileName) {
              // 
              filename = XHR.getResponseHeader('filename') || 'file.docx';
            } else {
              filename = XHR.getResponseHeader('filename') || 'file';
            }
            const a = document.createElement('a');
            a.download = `${filename}`;
            // 
            if (e.target) a.href = e.target.result;
            document.body.appendChild(a);
            a.click();
            document.body.removeChild(a);
          };
        }

        // XHR
        const response = {
          status: XHR.status,
          statusText: XHR.statusText,
          headers: responseHeaders,
          data: responseData,
        };

        // XHR.status  [200,300) 
        if (XHR.status >= 200 && XHR.status < 300) {
          token && sessionStorage.setItem('authToken', token);
          resolve(response);
        } else {
          reject(response);
        }
      };

      // 
      XHR.send(IsForm ? data : JSON.stringify({ ...data }));
      // 
      XHR.ontimeout = (error) => {
        reject(error);
        console.error('!');
      };
      // 
      XHR.onerror = (error) => {
        reject(error);
        console.error('!');
      };
    });
  }

  get(url, data, ...rest) {
    return this.request({
      method: 'GET',
      url,
      data,
      ...rest,
    });
  }

  delete(url, data, ...rest) {
    return this.request({
      method: 'DELETE',
      url,
      data,
      ...rest,
    });
  }

  post(url, data, ...rest) {
    return this.request({
      method: 'POST',
      url,
      data,
      ...rest,
    });
  }

  put(url, data, ...rest) {
    return this.request({
      method: 'PUT',
      url,
      data,
      ...rest,
    });
  }

  patch(url, data, ...rest) {
    return this.request({
      method: 'PATCH',
      url,
      data,
      ...rest,
    });
  }
}

export default new axios();
