import {
  DirectionalLight,
  DirectionalLightHelper,
  PointLight,
  PointLightHelper,
} from 'three';

/***
 * 
 */
const pointLight = new PointLight(0xffffff, 1);
pointLight.intensity = 8; //
pointLight.decay = 0; //
pointLight.position.set(40, 70, 60); //

const pointLightHelper = new PointLightHelper(pointLight, 4);
pointLightHelper.add(pointLight);

/**
 * 
 */

/**
 * 
 */
const directionalLight = new DirectionalLight(0xffffff, 1);
directionalLight.position.set(40, 50, 80);

const directionalLightHelper = new DirectionalLightHelper(directionalLight, 1);
directionalLightHelper.add(directionalLight);

export {
  pointLight,
  pointLightHelper,
  directionalLight,
  directionalLightHelper,
};
